@charset "UTF-8";
/* リキッドレイアウト対応 */
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.container {
  display: block;
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .container {
    display: flex;
  }
}

.footer {
  display: block;
  background: rgb(65, 105, 225);
  padding: 0 1.25rem;
  text-align: center;
}
.header {
  display: flex;
  height: 1.875rem;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: fixed;
  top: 0;
  background: rgb(255, 105, 180);
  padding: 0 1.25rem;
}
.header a {
  display: block;
  font-size: 1rem;
}

button.js-header-menu {
  display: flex;
  align-items: center;
  width: 1.875rem;
  height: 1.875rem;
  background: rgba(255, 192, 203, 0);
  border: 0;
  padding: 0.3125rem;
}
@media screen and (min-width: 768px) {
  button.js-header-menu {
    display: none;
  }
}

button.js-header-menu span {
  display: block;
  width: 100%;
  height: 0.125rem;
  background: rgb(51, 51, 51);
  position: relative;
}

button.js-header-menu span::before {
  content: "";
  display: block;
  width: 100%;
  height: 0.125rem;
  background: rgb(51, 51, 51);
  position: absolute;
  left: 50%;
  transform: translate(-50%, -8px);
}

button.js-header-menu span::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.125rem;
  background: rgb(51, 51, 51);
  position: absolute;
  left: 50%;
  transform: translate(-50%, 8px);
}

button.open span {
  background: rgba(51, 51, 51, 0);
}

button.open span::before {
  transform: rotate(45deg);
  left: 0;
}

button.open span::after {
  transform: rotate(-45deg);
  left: 0;
}

.main {
  display: block;
  background: rgb(255, 215, 0);
  height: 200dvh;
  padding: 0 1.25rem;
}
@media screen and (min-width: 768px) {
  .main {
    flex: 2;
  }
}

form.search-form input,
button.search-form-button {
  padding: 0.625rem;
}

@media screen and (min-width: 768px) {
  form.search-form {
    display: flex;
  }
}

form.search-form input {
  display: block;
  flex: 3;
  margin-right: 0.3125rem;
}

button.search-form-button {
  display: block;
  flex: 1;
}

.sidebar {
  display: none;
}
@media screen and (min-width: 768px) {
  .sidebar {
    display: block;
    background: rgb(60, 179, 113);
    padding: 1.25rem;
    flex: 1;
  }
}

.sidebar-list {
  margin-bottom: 1.875rem;
}